/* src/pages/Camera.css */

.camera-page {
    padding: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title {
    font-size: 32px;
    margin-bottom: 30px;
}

.video-container {
    border-radius: 20px;
    overflow: hidden;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    width: 640px;
    height: 480px;
    background: #000;
    margin-bottom: 30px;
}

.video-feed {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.btn-outline {
    padding: 12px 20px;
    border: 2px solid #1a1a1a;
    background: white;
    color: #1a1a1a;
    border-radius: 10px;
    font-size: 16px;
    cursor: pointer;
}

.btn-outline:hover {
    background: #1a1a1a;
    color: white;
}
